import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { ToastrModule, ToastrService } from 'ngx-toastr';
import { SearchPagination } from 'src/app/commons/interfaces/search.pagination';
import { DimensionModel } from 'src/app/commons/models/evaluacion-docente/mantenedores/dimensiones.model';
import { PreguntaModel } from 'src/app/commons/models/evaluacion-docente/mantenedores/preguntas.model';
import { MantenedoresEvaluacionDocenteService } from 'src/app/commons/services/evaluacion-docente-services/mantenedores.service';
import { UtilAlert } from 'src/app/commons/util/util.alert';

@Component({
  selector: 'app-mantenedor-preguntas',
  templateUrl: './mantenedor-preguntas.component.html',
  styles: [
  ]
})
export class MantenedorPreguntasComponent implements OnInit {

  public listadoDimensiones: DimensionModel[] = [];
  public listadoPreguntas: PreguntaModel[] = [];

  public isModoEdicion: boolean = false;
  public contenidoAEditar: 'pregunta' | 'dimension';
  public isEdit: boolean = false;

  public formPregunta: FormGroup;
  public formDimension: FormGroup;

  //** PAGINATION PREGUNTAS */
  public searchPagination: SearchPagination<string> = null;
  public pageSize: number = 10;
  public page: number = 1;
  public totalElements = 0;
  public buscador: string = '';
  public filtroDimension: string = '';

  public formTopBar: FormGroup;
  //** */

  public loading = false;
  @BlockUI('mantenedorEvaluacionDocentePreguntas') blockUI: NgBlockUI;

  constructor(
    private mantenedorService: MantenedoresEvaluacionDocenteService,
    private toastr: ToastrService,
    private utilAlert: UtilAlert
  ) { }

  ngOnInit(): void {
    this.initFormTopBar();
    this.listarDimensiones();
    this.listarPreguntas();
  }

  initFormTopBar() {
    this.formTopBar = new FormGroup({
      buscador: new FormControl(""),
      dimension: new FormControl(""),
      registros: new FormControl("")
    })

    this.formTopBar.get("buscador").setValue(this.buscador);
    this.formTopBar.get("dimension").setValue(this.filtroDimension);
    this.formTopBar.get("registros").setValue(this.pageSize.toString());
  }

  initFormPregunta(pregunta: PreguntaModel) {
    this.formPregunta = new FormGroup({
      codigo: new FormControl("", [Validators.required]),
      descripcion: new FormControl("", [Validators.required]),
      dimension: new FormControl("", [Validators.required])
    })

    this.isEdit = pregunta != null;
    console.log(pregunta);
    if (pregunta != null) {
      this.formPregunta.get("codigo").setValue(pregunta.codigo);
      this.formPregunta.get("descripcion").setValue(pregunta.descripcion);
      this.formPregunta.get("dimension").setValue(pregunta.dimension.codigo);
    }
  }

  initFormDimension(dimension: DimensionModel) {
    this.formDimension = new FormGroup({
      codigo: new FormControl("", [Validators.required]),
      descripcion: new FormControl("", [Validators.required])
    })

    this.isEdit = dimension != null;
    if (dimension != null) {
      this.formDimension.get("codigo").setValue(dimension.codigo);
      this.formDimension.get("descripcion").setValue(dimension.descripcion);
    }
  }

  listarDimensiones() {
    this.blockUI.start("Cargando dimensiones...");
    this.mantenedorService.listarDimensiones().subscribe(result => {
      if (result.length != 0) {
        this.listadoDimensiones = result;
        this.blockUI.stop();
      }
    }, (_error) => {
      this.blockUI.stop();
    })
  }


  listarPreguntas() {
    this.listadoPreguntas = [];
    if (this.searchPagination == null) {
      this.searchPagination = {
        page: this.page,
        records: this.pageSize,
        seek: this.buscador,
      }
    } else {
      this.searchPagination = null
      this.listarPreguntas();
    }
    this.mantenedorService.paginationPreguntas(this.searchPagination, this.filtroDimension).subscribe(result => {
      this.listadoPreguntas = result.content;
      this.totalElements = result.totalElements;
    })
  }

  filtrarPorCodigo(valor: string) {
    this.buscador = valor;
    this.listarPreguntas();
  }

  iniciarEdicionPregunta(pregunta: PreguntaModel) {
    this.initFormPregunta(pregunta);
    this.isModoEdicion = true;
    this.contenidoAEditar = 'pregunta';
  }

  iniciarEdicionDimension(dimension: PreguntaModel) {
    this.initFormDimension(dimension);
    this.isModoEdicion = true;
    this.contenidoAEditar = 'dimension';
  }

  saveUpdatePregunta() {
    if (!this.formPregunta.valid) {
      this.toastr.error("El formulario no es valido.");
    } else {
      this.blockUI.start("Guardando cambios...");
      let pregunta = new PreguntaModel();

      pregunta.codigo = this.formPregunta.get("codigo").value.toUpperCase();
      pregunta.descripcion = this.formPregunta.get("descripcion").value;
      pregunta.dimension = this.listadoDimensiones.filter(d => d.codigo == this.formPregunta.get("dimension").value)[0];

      if (this.isEdit) {
        this.mantenedorService.updatePregunta(pregunta).subscribe(result => {
          this.volver();
          this.listarPreguntas();
          this.utilAlert.infoSwal("Pregunta editada exitosamente.");
        }, (_error) => {
          this.toastr.error(_error?.error?.message);
        })
      } else {
        this.mantenedorService.savePregunta(pregunta).subscribe(result => {
          this.volver();
          this.listarPreguntas();
          this.utilAlert.infoSwal("Pregunta creada exitosamente.");
        }, (_error) => {
          this.toastr.error(_error?.error?.message);
        })
      }
      this.blockUI.stop();
    }
  }

  saveUpdateDimension() {
    if (!this.formDimension.valid) {
      this.toastr.error("El formulario no es valido.");
    } else {
      this.blockUI.start("Guardando cambios...");
      let dimension = new DimensionModel();

      dimension.codigo = this.formDimension.get("codigo").value.toUpperCase();
      dimension.descripcion = this.formDimension.get("descripcion").value;

      let existeDimension = this.listadoDimensiones.filter(d => d.codigo == dimension.codigo).length != 0;

      if (!this.isEdit && existeDimension) {
        this.toastr.error("Ya existe la dimension con el código " + dimension.codigo);
      } else {
        this.mantenedorService.saveDimension(dimension).subscribe(result => {
          this.volver();
          this.listarDimensiones();
          let msg = this.isEdit ? "editada" : "creada";
          this.utilAlert.infoSwal("Dimensión " + msg + " exitosamente.");
        }, (_error) => {
          this.toastr.error(_error?.error?.message);
        })
      }
      this.blockUI.stop();
    }
  }

  volver() {
    this.isModoEdicion = false;
    this.contenidoAEditar = null;
  }

}
